@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
html {
  --text-color-light: black;
  --text-color-dark: #ccc;
  --bg-color-light: white;
  --bg-color-dark: #181818;
  --accent-color-light: #013d5b;
  --accent-color-dark: #222;
  --border-color-light: #aaa;
  --border-color-dark: #333;
  --search-color-light: #e8e8e8;
  --search-color-dark: #181818;
}

body {
  margin: 0;
  font-family: 'Montserrat', Arial, sans-serif;
  background-color: #013d5b;
  background-color: var(--accent-color-light);
}

@media screen and (prefers-color-scheme: dark) {
  body {
    background-color: #222;
    background-color: var(--accent-color-dark);
  }
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
}

.header {
  background-color: var(--accent-color-light);
  min-height: 100px;
  color: var(--bg-color-light);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

#icon {
  width: 60px;
  position: absolute;
  top: 20px;
  left: 20px;
}

@media screen and (prefers-color-scheme: dark) {
  .header {
    background-color: var(--accent-color-dark);
    color: var(--text-color-dark);
  }

  #icon {
    opacity: 0.77;
  }
}

.info {
  width: 100%;
  margin: auto;
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
  padding-bottom: 20px;
  background-color: white;
}

form * {
  font-size: 16px;
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
  border-width: 1px;
  font-family: 'Montserrat', Arial, sans-serif;
}

#search-header {
  padding: 20px 0;
  width: 100%;
  background-color: var(--search-color-light);
  border-style: solid;
  border-width: 0 0 1px 0;
  border-color: var(--border-color-light);
}

#input {
  border-style: solid;
  border-color: var(--border-color-light);
}

#input:focus {
  outline-style: none;
  border-color: var(--text-color-light);
}

#search {
  padding: 10px;
  color: var(--bg-color-light);
  background-color: green;
  border-style: solid;
  border-color: darkgreen;
  cursor: pointer;
}

#search:active {
  border-color: limegreen;
}

.nav {
  font-family: 'Montserrat', Arial, sans-serif;
  padding: 10px;
  margin: 10px 10px 0 10px;
  border-radius: 5px;
  border-style: solid;
  border-width: 1px;
  border-color: var(--border-color-light);
}

.nav:active {
  border-color: var(--text-color-light);
}

.nav:enabled {
  background-color: var(--bg-color-light);
  cursor: pointer;
}

.nav:disabled {
  background-color: var(--search-color-light);
}

.nav:active {
  color: black;
}

.card {
  width: 50%;
  margin: auto;
}

@media screen and (prefers-color-scheme: dark) {
  .info {
    background-color: var(--bg-color-dark);
  }

  #search-header {
    background-color: var(--bg-color-dark);
    border-style: solid;
    border-width: 0 0 1px 0;
    border-color: var(--border-color-dark);
  }

  #input {
    background-color: var(--accent-color-dark);
    border-color: var(--border-color-dark);
    color: var(--text-color-dark);
  }

  #search {
    color: var(--text-color-dark);
    background-color: rgba(0, 100, 0, 0.8);
  }

  #description {
    color: var(--text-color-dark);
  }

  .nav {
    border-color: var(--border-color-dark);
  }

  .nav:active {
    border-color: var(--text-color-dark);
  }

  .nav:enabled {
    background-color: var(--accent-color-dark);
    color: var(--text-color-dark);
  }

  .nav:disabled {
    background-color: var(--bg-color-dark);
    color: var(--border-color-dark);
  }
}

.card {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-style: solid;
  border-width: 1px 0 0 0;
  padding: 5% 0;
}

.card:first-child {
  border-style: none;
}

.card-image {
  max-width: 100px;
  vertical-align: middle;
}

.image {
  -webkit-flex: 20% 1;
          flex: 20% 1;
}

.description {
  -webkit-flex: 80% 1;
          flex: 80% 1;
  margin: 0 5%;
}

.title {
  font-size: 20px;
  padding: 10px;
}

.more-info {
  background-color: var(--search-color-light);
  font-family: 'Montserrat', Arial, monospace;
  padding: 10px;
  border-radius: 5px;
  border-style: solid;
  border-width: thin;
  border-color: var(--border-color-light);
  cursor: pointer;
}

.more-info:active {
  border-color: var(--text-color-light);
}

/* Prevent scrolling when modal dialogue is open */
.ReactModal__Body--open {
  overflow: hidden;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: var(--bg-color-light);
  overflow: auto;
  border-radius: 10px;
  padding: 20px;
}

.modal-overlay {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.1);
}

.modal-info {
  text-align: center;
}

.score {
  font-weight: bold;
}

.great {
  color: green;
}

.good {
  color: limegreen;
}

.ok {
  color: gold;
}

.bad {
  color: orange;
}

.awful {
  color: red;
}

@media screen and (prefers-color-scheme: dark) {
  .card {
    border-color: var(--border-color-dark);
  }

  .title {
    color: var(--text-color-dark);
  }

  .more-info {
    background-color: var(--accent-color-dark);
    border-color: var(--border-color-dark);
    color: var(--text-color-dark);
  }

  .more-info:active {
    border-color: var(--text-color-dark);
  }

  .modal {
    background-color: var(--bg-color-dark);
    border-style: solid;
    border-width: 1px;
    border-color: var(--border-color-dark);
  }

  .modal-info {
    color: var(--text-color-dark);
  }
}

.footer {
  background-color: var(--accent-color-light);
  font-size: 20px;
  color: var(--bg-color-light);
}

h4 {
  font-weight: normal;
}

a {
  color: var(--bg-color-light);;
}

@media screen and (prefers-color-scheme: dark) {
  .footer {
    background-color: var(--accent-color-dark);
    color: var(--text-color-dark);
  }

  a {
    color: var(--text-color-dark);
  }
}

